export const getButtonText = ({ name, value }) => {
  switch (name) {
    case 'category':
      return value;
    case 'available':
      return ' ';
    case 'discount':
      return ' ';
    case 'price':
      return ` ${value[0]}  ${value[1]}`;
    case 'producer':
      return `: ${value}`;
    default:
      return null;
  }
};
